export default {
	props: {
		imgList: {
			type: Array,
		},
		heightNum: {
			type: Number,
			default: 662
		},
		autoplay: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			dotIndex: 0,
			circular: true,
		};
	},
	methods: {
		changeDot: function(e) {
			this.dotIndex = e.detail.current
		},
		linkToPage: function(value) {
			uni.navigateTo({
				url: value,
				fail(e) {
					console.warn(e.errMsg);
				}
			});
		}
	},
}
